	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨看国际政治学会的《《PARTICIPATION》登了今年国际大会的主题和报告，主题是\"民主化\"，下面分很多分专题。民主化的问题在今天世界越来越重要，也是社会基本的发展方向。今年的国际大会是在柏林开。中国政治学会已经退出国际政治学会。主题不错，在中国也要好好加以研究。</p>\n<p>另外看了中国政治学会的论文集《政治稳定》，好的文章不多。系里有几位研究生在上面发表了文章。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0016.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0016.md","frontmatter":{"title":"1月24日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0016.md absPath of file >>> MarkdownRemark"}}

